<template>
<div>
    <el-row>
        <el-col :span="10">
            <div class="grid-content bg-purple">
                <el-tabs>
                    <el-tab-pane label="会员">
                        <el-table :data="userTableData" border style="width: 100%;height:100%">
                            <el-table-column prop="vipId" label="会员ID"></el-table-column>
                            <el-table-column prop="name" label="会员名"></el-table-column>
                            <el-table-column prop="phoneNum" label="电话号码"></el-table-column>
                            <el-table-column prop="gender" label="性别"></el-table-column>
                            <el-table-column prop="vipId" label="操作">
                                <template slot-scope="scope">
                                    <el-button type="primary" size="small" @click="setVip(scope.row)">修改</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-tab-pane>
                      <el-pagination background layout="prev, pager, next" @current-change="changePage" :total="50">
                        </el-pagination>
                </el-tabs>
            </div>
        </el-col>
        <el-col :span="5">
            <div class="grid-content bg-purple-light">
                <el-tabs>
                    <el-tab-pane label="设置">
                        当前折扣：{{cut}}
                        <!-- Form -->
                        <el-button type="primary" @click="dialogFormVisible = true">修改折扣</el-button>
                        <el-dialog title="修改折扣" :visible.sync="dialogFormVisible" width="300px">
                            <el-form :model="form">
                                <el-form-item label="折扣" :label-width="formLabelWidth">
                                    <el-input v-model="form.cut" type="text" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button type="info" @click="dialogFormVisible = false">取 消</el-button>
                                <el-button type="primary" @click="setCut">确 定</el-button>
                            </div>
                        </el-dialog>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </el-col>

          <el-col :span="8">
            <div class="grid-content bg-purple-light">
                <el-tabs>
                    <el-tab-pane label="添加会员" >                                       
                         <el-main><addVip></addVip></el-main>              
                    </el-tab-pane>
                </el-tabs>
            </div>
        </el-col>

    </el-row>
</div>
</template>

<script>
import {
    queryVip,
    setDiscount,
    queryDiscount
} from '../api/vip'

import addVip from '../components/addVip'
export default {
    data() {
        return {
            userTableData: [],
            cut: 0,
            dialogFormVisible: false,
            formLabelWidth: '60px',
            form: {
                cut: 0
            },

        }
    },
    components: {addVip},
    computed: {

    },
    methods: {
        setCut() {
            let cut = this.form.cut
            if (cut > 0 && cut < 1) {
                setDiscount(cut).then((res) => {
                    if (res.data.status == 1) {
                        alert("修改成功！")
                        this.dialogFormVisible = false
                        queryDiscount().then((res) => {
                            if (res.data.status == 1) {
                                this.cut = res.data.discount
                            }
                        })
                    }
                })
            } else {
                alert("请设置正确的折扣！")
            }
        },
        changePage(val) {
            //每页显示多少条数据
            let pageSize = 10;
            alert(val)
        }
    },
    created: function () {
        // `this` 指向 vm 实例
        console.log("正在初始化用户数据，请稍后......")
        queryVip().then((res) => {
            this.userTableData = res.data.vips
        })

        queryDiscount().then((res) => {
            if (res.data.status == 1) {
                this.cut = res.data.discount
            }
        })

    }
}
</script>

<style scoped>
#pane-0{
    height: 500px;
}
</style>



